* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

a:visited, a:active {
  color: inherit;
}

.body {
  min-height: calc(100vh - 24px);
  background-image: linear-gradient(to right top, #edc527, #d4d02d, #b9d93d, #9be052, #77e76b, #61e783, #4ce699, #3be5ac, #57ddba, #74d4c2, #90cbc4, #aac0bf);
}

.header {
  display: flex;
  align-items: center;
  margin: 24px auto 48px;
  padding: 0 24px;
  max-width: 960px;
  height: 48px;
}

.header .logo {
  height: 100%;
  color: white;
}

.header .logo img {
  height: 100%;
}

.header .link {
  margin-left: auto;
  height: 32px;
}

.header .link img {
  height: 100%;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.headmaster {
  margin-bottom: 24px;
  height: 196px;
}

.text-area {
  margin-top: 48px;
  width: 300px;
  text-align: center;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 148px auto 48px;
  padding: 0 24px;
  max-width: 720px;
  font-size: 14px;
}

.actions {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.actions .btn + .btn {
  margin-top: 24px;
}

.btn:link,
.btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 15px 40px;
    display: inline-block;
    border-radius: 100px;
    transition: all .2s;
    position: relative;
}

.button-wrapper {
  margin-top: 44px;
  /* width: 192px; */
}

.btn {
  background-color: #51cf66;
  color: #343a40;
}

.btn:hover {
    transform: translateY(-3px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn:active {
    transform: translateY(-1px);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.btn::after {
    content: "";
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 100px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: all .4s;
}

.btn::after {
    /* background-color: #fff; */
}

.btn:hover::after {
    transform: scaleX(1.4) scaleY(1.6);
    opacity: 0;
}

.btn-animated {
    animation: moveInBottom 5s ease-out;
    animation-fill-mode: backwards;
}

@keyframes moveInBottom {
    0% {
        opacity: 0;
        transform: translateY(30px);
    }

    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}
